import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Row, Col, ListGroup, Image, Form, Button, Card } from 'react-bootstrap';

import { useDispatch, useSelector } from 'react-redux';

import { addToCart, removeFromCart } from '../actions/cartActions';
import Message from '../Components/Message';

const CartScreen = ({ match, location, history }) => {
  const productId = match.params.id;

  const qty = location.search ? Number(location.search.split('=')[1]) : 1;

  const dispatch = useDispatch();

  const cart = useSelector((state) => state.cart);

  const { cartItems } = cart;

  useEffect(() => {
    if (productId) {
      dispatch(addToCart(productId, qty));
    }
  }, [dispatch, productId, qty]);

  const removeFromCartHandler = (id) => {
    dispatch(removeFromCart(id));
  };
  const checkoutHandler = () => {
    history.push('/login?redirect=shipping');
    // stuff
  };
  return (
    <Row>
      <Col md={8}>
        <h1> Shopping Cart </h1>

        {cartItems.length === 0 ? (
          <Message>
            {' '}
            Your cart is empty <Link to='/'> Go Back</Link>
          </Message>
        ) : (
          <ListGroup className='py-3' variant='flush'>
            {cartItems.map((item) => (
              <ListGroup.Item className='py-3 my-3' key={item.product}>
                <Row>
                  <Col md={2}>
                    <Image src={item.image} alt={item.name} fluid rounded />
                  </Col>

                  <Col md={3}>
                    <Link to={`/product/${item.product}`}>{item.name}</Link>
                  </Col>

                  <Col md={2}>${item.price}</Col>
                  <Col md={2}>
                    {' '}
                    <Form.Control as='select' value={item.qty} onChange={(e) => dispatch(addToCart(item.product, Number(e.target.value)))}>
                      {[...new Array(item.countInStock).keys()].map((x) => (
                        <option key={x + 1} value={x + 1}>
                          {x + 1}
                        </option>
                      ))}
                    </Form.Control>{' '}
                  </Col>
                  <Col md={2}>
                    <Button type='button' variant='light' onClick={() => removeFromCartHandler(item.product)}>
                      <i className='fas fa-trash'> </i>
                    </Button>
                  </Col>
                </Row>
              </ListGroup.Item>
            ))}
          </ListGroup>
        )}
      </Col>
      <Col md={4}>
        <Card>
          <ListGroup className='py-3 m-3' variant='flush'>
            <ListGroup.Item>
              <h2> Subtotal ({cartItems.reduce((acc, item) => acc + item.qty, 0)}) items </h2>
              <h5>${cartItems.reduce((acc, item) => acc + item.qty * item.price, 0).toFixed(2)}</h5>
            </ListGroup.Item>

            <ListGroup.Item>
              <Button type='button' variant='success' className='btn col-12' disabled={cartItems.length === 0} onClick={checkoutHandler}>
                {' '}
                Proceed To Checkout{' '}
              </Button>
            </ListGroup.Item>
          </ListGroup>
        </Card>
      </Col>
      <Col md={2}></Col>
    </Row>
  );
};

export default CartScreen;
